/* 탭 바 스타일 */
.tab-bar {
  display: flex;
  align-items: center;
  background: var(--bg-secondary);
  height: 38px;
  padding: 0 8px;
  gap: 2px;
  z-index: 1100;
  position: relative;
  flex-shrink: 0;
  margin: 0;
}

.tab-ezreader {
  display: flex;
  align-items: center;
  gap: 8px;
}

.tab-ezreader-logo {
  height: 13px;
  width: auto;
  display: block;
  margin-right: 10px;
}

.tab-home-icon {
  margin-right: 8px;
  display: flex;
  align-items: center;
}

.tab {
  display: flex;
  align-items: center;
  background: var(--bg-primary);
  border-radius: 6px 6px 0 0;
  margin-right: 10px;
  padding: 0 16px 0 8px;
  font-size: 13px;
  color: var(--text-primary);
  height: 32px;
  position: relative;
  gap: 6px;
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
  margin-bottom: -4px;
  min-width: 0;
}

.tab-name-ellipsis {
  display: block;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tab.active {
  background: var(--bg-primary);
  color: var(--accent-color);
  border-bottom: 2px solid var(--accent-color);
  font-weight: 600;
  z-index: 1;
}

.tab-close-btn {
  background: none;
  border: none;
  font-size: 15px;
  color: var(--text-muted);
  cursor: pointer;
  margin-left: 6px;
  padding: 0 2px;
  border-radius: 3px;
  transition: background 0.15s;
  position: static;
  top: auto;
  right: auto;
  transform: none;
}

.tab-close-btn:hover {
  background: var(--bg-tertiary);
}

.tab-add-btn {
  background: var(--bg-secondary);
  color: var(--text-muted);
  border: 0px solid var(--border-color);
  border-radius: 6px 6px 0 0;
  font-size: 18px;
  width: 32px;
  height: 32px;
  margin-left: 2px;
  cursor: pointer;
  transition: background 0.15s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tab-add-btn:hover {
  background: var(--bg-tertiary);
}

.logout-btn {
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 8px;
  margin-left: auto;
  margin-right: 8px;
  border-radius: 4px;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: var(--bg-tertiary);
  }
}
